.nav {
  justify-self: end;
  text-align: center;
  font-weight: 500;

  &__link {
    display: inline-block;
    position: relative;
    text-decoration: none;
    padding: 23px 0;
    text-transform: uppercase;
    color: #000;
    font-size: 12px;
    transition: color 400ms;

    &:hover {
      color: $light-blue-color;
    }

    &--active {
      color: $light-blue-color;
    
      &:after {
        content: "";
        position: absolute;
        background: $light-blue-color;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 4px;
        border-radius: 8px;
      }
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(8, min-content);
    gap: 20px;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    
    &:first-child {
      margin-left: 0;
    }

    &:last-child {
      margin-right: 0;
    }
  }
}
